import React                                        from 'react';
import {
    connect
}                                                   from 'react-redux';




/* ################################################### */
/*          ui framework         */
/* ################################################### */

import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';
import {
    Table,
    TableBody,
    TableHeader,
    TableHeaderColumn,
    TableRow, TableRowColumn }                      from 'material-ui/Table';
import TextField                                    from 'material-ui/TextField';

import Paper                                        from 'material-ui/Paper';
import FlatButton                                   from 'material-ui/FlatButton';
import RaisedButton                                 from 'material-ui/RaisedButton';
import Divider                                      from 'material-ui/Divider';
import Dialog                                       from 'material-ui/Dialog';

import SelectField                                  from 'material-ui/SelectField';
import MenuItem                                     from 'material-ui/MenuItem';
import DatePicker                                   from 'material-ui/DatePicker';
import TimePicker                                   from 'material-ui/TimePicker';






/* ################################################### */
/*          redux         */
/* ################################################### */

import ReduxActionServicePackage , { ReduxEnumServicePackage }  from '../../../_redux/action/reduxAction.servicePackage';









/* ################################################### */
/*          helper         */
/* ################################################### */
import HelperNavigate , { HelperNavigateENUM }		from '../../_helper/h.navigate';
import HelperStorage		                        from '../../_helper/h.storage';










/* ################################################### */
/*          abstract         */
/* ################################################### */

import ComponentAbstract                            from '../../_abstract/c.abstract';










/* ################################################### */
/*          component         */
/* ################################################### */

import ComponentCustomLoading                       from '../../_component/loading/cc.loading';











import './s.servicePackage.list.css';







export default connect(
        ( state ) => {

            let thisState = state[ 'ReduxServicePackage' ] ;
            let stateObj = { };

            if ( thisState ) {

                for ( let i = 0 ; i < thisState.length; i ++ ) {
                    let recordItem = thisState[ i ];
                    if ( !recordItem[ 'completed' ] ) {

                        if ( ReduxEnumServicePackage.SEARCH_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestSearchDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        }

                        recordItem[ 'completed' ] = true;
                    }
                }

            }

            return stateObj;

        }
    ) (

        class ComponentServicePackageList extends ComponentAbstract {


            constructor( props ) {
                super( props )

                this.state = {

                    data : {}


                    , form : {
                        code : ''
                        , type : ''
                    }


                    , result : []

                }

            }









            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        event
                ################################################################################################################################################
                ################################################################################################################################################
            */

            onClickDetail( spCode ) {

                console.log( 'ComponentServicePackageList : onClickDetail : *** : spCode => ' , spCode );

                let record = {};
                for ( let i = 0; i < this.state.result.length; i ++ ) {
                    let tmpRecord = this.state.result[ i ];

                    if ( tmpRecord[ 'spCode' ] == spCode ) {
                        record = tmpRecord;
                    }

                }

                HelperNavigate.route(
                    HelperNavigate.mapPath( HelperNavigateENUM.SERVICE_PACKAGE_DETAIL )
                    , record
                );


            }












            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        request
                ################################################################################################################################################
                ################################################################################################################################################
            */

            requestSearch() {
                console.log( 'ComponentServicePackageList : requestSearch : *** ' );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionServicePackage.rSearch(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                        , {
                                            code : this.state.form.code
                                            , type : this.state.form.type
                                        }
                                    )
                                )
                            }
                        );

                    }
                )
            }
            requestSearchDone( result ) {
                console.log( 'ComponentServicePackageList : requestSearchDone : ***  : result => ' , result );

                let data = this.state.data;
                this.setState(
                    {
                        isLoading : false
                        , result : result
                    }
                );
                this.dispatch( ReduxActionServicePackage.rRequestEnd( ) );
            }












            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        render
                ################################################################################################################################################
                ################################################################################################################################################
            */

            render() {
                return (
                        <div>
                            <Paper style={ { width : '70%', margin : '0px auto' } }>
                                
                                    <h3 style={ { paddingLeft: 25, paddingTop: 12 } }>Service Package</h3>

                                    <hr/>

                                <div style={ { paddingLeft: 25, paddingBottom: 10  } }>


                                    <div style={ { display: 'flex' , justifyContent: 'flex-start' } }>
                                                
                                        <div style={ { flex: 0.15 } } >
                                            <p style={ { paddingTop: 12 } }>Service Package Code</p>
                                        </div>

                                        <TextField
                                                style={ { flex: 0.25 } }
                                                hintText={ 'Code' }
                                                value={ this.state.form.code }

                                                onChange={
                                                    ( event , newValue ) => {
                                                        let form = this.state.form;
                                                        form[ 'code' ] = newValue;
                                                        this.setState( { form : form } );

                                                        }
                                                }

                                        />

                                    </div>


                                    <div style={ { display: 'flex' , justifyContent: 'flex-start' } }>
                                                
                                        <div style={ { flex: 0.15 } } >
                                            <p style={ { paddingTop: 12 } }>Service Package Name</p>
                                        </div>

                                        <TextField
                                                style={ { flex: 0.25 } }
                                                hintText={ 'Type' }
                                                

                                                onChange={
                                                    ( event , newValue ) => {
                                                        let form = this.state.form;
                                                        form[ 'type' ] = newValue;
                                                        this.setState( { form : form } );
                                                    }
                                                }
                                        />

                                    </div>


                                     <div style={ { display: 'flex' , justifyContent: 'flex-start' } }>
                                                
                                        <div style={ { flex: 0.15 } } >
                                            <p style={ { paddingTop: 32 } }>Type</p>
                                        </div>

                                        <SelectField
                                                    style={ { flex: 0.25 } } 
                                                    floatingLabelText={ "Action" }
                                                   
                                                    onChange={
                                                        ( event , index , value ) => {
                                                           
                                                        }
                                                    } >
                                                    <MenuItem   value={ 'ALL' }        primaryText={ 'ALL' } />
                                                    <MenuItem   value={ 'ALL' }        primaryText={ 'e-Inquiry' } />
                                                    <MenuItem   value={ 'ALL' }        primaryText={ 'e-Payment' } />
                                                  
                                        </SelectField>
                                    </div>
                                  
                                    <div style={ { display: 'flex' , justifyContent: 'flex-end', width: '40%', padding: '10px 0' } }>

                                                <div style={ { flex: 0.1 } } />
                                                
                                                <RaisedButton
                                                    style={ { flex: 0.2 } }
                                                    label={ "Search" }
                                                    onClick={ () => {
                                                        this.requestSearch();
                                                    } }
                                                   />

                                            
                                    </div>
                                   
                                </div>


                                <Divider />


                                <Table
                                            fixedHeader={ false }
                                            selectable={ false }
                                            style={ { width: "100%" } }
                                            tableLayout="auto">

                                    <TableHeader
                                                    displaySelectAll={ false }
                                                    adjustForCheckbox={ false }>

                                        <TableRow>

                                            <TableHeaderColumn> Code </TableHeaderColumn>
                                            <TableHeaderColumn> Name </TableHeaderColumn>
                                            <TableHeaderColumn> Type </TableHeaderColumn>
                                            <TableHeaderColumn> Description </TableHeaderColumn>
                                            <TableHeaderColumn> Accesible Function </TableHeaderColumn>

                                        </TableRow>

                                    </TableHeader>

                                    <TableBody

                                                displayRowCheckbox={ false }>

                                        {
                                            this.state.result.map(
                                                ( item , index ) => {
                                                    return (
                                                        <TableRow
                                                                    key={ '_servicePackage_record_' + index } >

                                                            <TableRowColumn> { item.spCode } </TableRowColumn>

                                                            <TableRowColumn> { item.spName } </TableRowColumn>

                                                            <TableRowColumn> { item.spType } </TableRowColumn>

                                                            <TableRowColumn> { item.soDesc } </TableRowColumn>

                                                            <TableRowColumn> - </TableRowColumn>
                                                            
                                                        </ TableRow>
                                                    )
                                                }
                                            )
                                        }

                                    </TableBody>

                                </Table>

                            </Paper>


                            <ComponentCustomLoading
                                visible={ this.state.isLoading }
                            />

                        </div>
                )
            }
        }

    )



